<template>
<div>
    <el-row class="login">
        <el-col :push="14" :span="5" class="login-col">
            <el-form ref="loginForm" class="form_box" :model="user" :rules="rules" status-icon>
                <el-form-item prop="id">
                    <el-input type="text" v-model.trim="user.id" placeholder="请输入帐号"  suffix-icon="iconfont aibd-icon-user"></el-input>
                </el-form-item>
                <el-form-item prop="pass">
                    <el-input type="password" v-model.trim="user.pass" placeholder="请输入密码" suffix-icon="iconfont aibd-icon-password"></el-input>
                </el-form-item>
                <el-form-item prop="code">
                    <el-input type="text" v-model.trim="user.code" placeholder="请输入验证码" suffix-icon="">
                        <el-tooltip slot="append" effect="dark" content="看不清，换一张" placement="right-end">
                            <div class="codeimg"><img :src="imgsrc" @click="clickIMG"   ref="img" name="img" alt="看不清，换一张" title="看不清，换一张"></div>
                        </el-tooltip>
                    </el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" class="btn-block" @click="loginByTel" :disabled="disabled" :loading="loading"><span v-show="!loading&&!disabled">登录</span><span v-show="loading">登录中</span><span v-show="disabled">登录成功</span></el-button>
                </el-form-item>
                <el-form-item><a href="#/rigster" class="float-rt">注册新用户</a></el-form-item>
            </el-form>
        </el-col>
    </el-row>
</div>
</template>

<script>
export default {
    data: function () {
        return {
            user: {
                id: "",
                pass: "",
                code: ""
            },
            rules: {
                id: [{
                    required: true,
                    message: '请输入登录帐号',
                    trigger: 'blur'
                }],
                pass: [{
                    required: true,
                    message: '请输入密码',
                    trigger: 'blur'
                }],
                code: [{
                    required: true,
                    message: '请输入验证码',
                    trigger: 'blur'
                }]
            },
            imgsrc: `${process.env.VUE_APP_apiUrl}/captcha.jpg`,
            loading: false,
            disabled:false
        };
    },
    methods: {
        //换验证码图片
        clickIMG() {
            this.$refs.img.src = this.imgsrc + "?" + new Date();
        },
        loginByTel() {
            let vm = this;
            vm.loading = true;
            let user = vm.user;
            vm.$refs['loginForm'].validate(bool => {
                if (bool) {
                    vm.$store.dispatch("login_user", vm.utils.data.formatter(user)).then(success => {
                        vm.$message({
                            type: "success",
                            message: "登录成功",
                            onClose: () => {
                                location.href = "/"
                            }
                        })
                        vm.disabled=true;
                    }).catch(error => {
                        vm.clickIMG();
                        vm.user.pass = vm.user.code = "";
                    }).finally(()=>vm.loading=false);
                } else {
                    vm.loading = false;
                }
            })
        }
    }
};
</script>

<style scoped>
.login-col {
    min-width: 400px;
}

.control-label {
    text-align: left !important;
    padding-right: 0;
}

.codeimg {
    height: 38px;
    width: 100px;
    position: relative;
    cursor: pointer;
}

.codeimg img {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
}

.form_box {
    position: relative;
    top: 10rem;
    padding: 2rem;
    box-sizing: border-box;
    background-color: rgba(255, 255, 255, 0.95);
    border-radius: 0.2rem;
    border: 1px solid rgba(0, 0, 0, 0.144);
    box-shadow: 5px 10px 10px rgba(150, 176, 223, 0.5);
    /* max-width: 450px;
    min-height: 350px; */
}

.form_box .form-group {
    height: 55px;
}

.form_box .form-group input,
.form_box .form-group button {
    height: 50px;
    border-radius: 0;
}

.form_box .form-group .login-btn {
    min-width: 150px;
    margin: auto;
}

.login a {
    color: rgb(8, 8, 8) !important;

}

.login-btn {
    width: 100%;
}

.visi {
    visibility: hidden;
}

.radio_class {
    text-align: center;

}

.radio_class label {
    margin-left: 0 !important;
}
</style>
